const AboutRevealSlides = () => (
  <Slide>
    <SlideWithTitle title="Introdução ao GIT">
      <h6>A base sobre Versionamento de código</h6>
      <img
        width="250px"
        src="image/git-logo.png"
        alt="QR code that links to these slides"
      />
      <p>GIT </p>
      <Note>What is GIT?</Note>
    </SlideWithTitle>
    <Slide transition="slide">
      <h4>O que é GIT ?</h4>
      <aside>
        "De longe, o sistema de controle de versão moderno mais usado no mundo
        hoje é o Git. O Git é um projeto de código aberto maduro e com
        manutenção ativa desenvolvido em 2005 por Linus Torvalds, o famoso
        criador do kernel do sistema operacional Linux". Fonte: Atlassian
      </aside>
      <br />
      <div className="row">
        <div className="column">
          <ul>
            <li>Controle de Versão</li>
            <li>Gerênciamento de Histórico</li>
            <li>Rastreio de Alterações</li>
          </ul>
        </div>
        <div className="column">
          <img
            width="175px"
            src="image/git-logo.png"
            alt="QR code that links to these slides"
          />
        </div>
      </div>
    </Slide>
    <Slide transition="slide">
      <h4>O que é GITHUB ?</h4>
      <aside>
        "GitHub é uma plataforma de hospedagem de código-fonte e arquivos com
        controle de versão usando o Git". - Wikipedia
      </aside>
      <br />
      <img width="350px" src="image/octocat.png" alt="github" />
      <p>Mascote: Octocat</p>
    </Slide>
    <Slide transition="slide">
      <h4>GITHUB</h4>
      <aside>
        "Ele permite que programadores, utilitários ou qualquer usuário
        cadastrado na plataforma contribuam em projetos privados e/ou Open
        Source de qualquer lugar do mundo". - Wikipedia
      </aside>
      <aside>
        "GitHub é amplamente utilizado por programadores para divulgação de seus
        trabalhos ou para que outros programadores contribuam com o projeto,
        além de promover fácil comunicação através de recursos que relatam
        problemas ou mesclam repositórios remotos (issues, pull request)." -
        Wikipedia
      </aside>
    </Slide>
    <Slide transition="slide">
      <h4>GITHUB</h4>
      <aside>
        "O GitHub é mundialmente usado e chega a ter mais de 36 milhões de
        usuários ativos mundialmente contribuindo em projetos comerciais ou
        pessoais. Em 2017 o GitHub já abrigava mais de 100 milhões de projetos."
        - Wikipedia
      </aside>
      <br />
      <img width="250px" src="image/ironcat.png" alt="github" />
    </Slide>
    <Slide transition="slide">
      <h4>Plataforma de Colaboração de Código Aberto</h4>
      <aside>
        "Open Source e Software Livre não são opostos, porém não são sinônimos
        também, sendo bastante comum a confusão entre os dois." - Alura
      </aside>
      <aside>
        "Software Livre é um movimento filosófico e político cujo objetivo é
        garantir à comunidade de usuários a liberdade de executar, copiar,
        distribuir, estudar, mudar e melhorar o software em questão, não
        necessariamente significando a monetização do produto. A partir desses
        conceitos, um novo conjunto de técnicas e metodologias para
        desenvolvimento de software foi ganhando forma." - Alura
      </aside>
      <aside>
        "Open Source se refere, justamente, ao modelo de negócio empregado pela
        comunidade do software livre para o desenvolvimento de seus projetos,
        sem conexão à filosofia característica do movimento." - Alura
      </aside>
    </Slide>
    <Slide transition="slide">
      <h4>Casos de Colaboração de Código Aberto</h4>
      <aside>
        "Um dos mais famosos projetos de software a utilizar o Open Source como
        modelo de desenvolvimento é o Linux ou, mais precisamente, Kernel do
        Linux. Desenvolvido em 1991, por Linus Torvalds" - Alura
      </aside>
      <br />
      <img width="350px" src="image/linux.png" alt="github" />
    </Slide>
    <Slide transition="slide">
      <h4>Casos de Colaboração de Código Aberto</h4>
      <div className="column">
        <img width="450px" src="image/bitcoin.png" alt="github" />
      </div>
      <div className="column">
        <Link href="https://github.com/bitcoin/bitcoin">BITCOIN</Link>
      </div>
    </Slide>
    <Slide transition="slide">
      <h4>Outras Plataformas de Hospedagem</h4>
      <aside>
        "Assim como o git não é a única forma de versionar código. O Github não
        é a única plataforma de hospedagem."
      </aside>
      <br />
      <div className="row">
        <div className="column">
          <img
            height="250px"
            width="250px"
            src="image/gitlab.png"
            alt="github"
          />
        </div>
        <div className="column">
          <img
            height="250px"
            width="250px"
            src="image/gitkraken.jpg"
            alt="github"
          />
        </div>
        <div className="column">
          <img
            height="250px"
            width="250px"
            src="image/bitbucket.png"
            alt="github"
          />
        </div>
      </div>
    </Slide>
    <Slide transition="slide">
      <iframe
        src="https://giphy.com/embed/14bvjOzMN8s0x2"
        width="450"
        height="450"
        frameBorder="0"
        allowFullScreen
        title="tired"
      />
      <h4>Dúvidas?</h4>
    </Slide>
    <Slide transition="slide">
      <div className="column">
        <img
          height="250px"
          width="250px"
          src="image/exercises-101.png"
          alt="github"
        />
      </div>
      <div className="column">
        <Link
          href="https://github.com/padolabs/web-development-101"
          target="blank"
        >
          Exercícios
        </Link>
      </div>
    </Slide>
    {/*  <Slide>
      <JSX code={revealExample} />
      <Note>
        <ul>
          <li>
            The slide you just saw for a short into into Reveal, this is what
            the code looks like
          </li>
        </ul>
      </Note>
    </Slide>
    <Slide>
      <h3>Features of Reveal</h3>
      <ul>
        <li>
          HTML presentation - so you can use all your favourite web libraries
          and frameworks (like React)
        </li>
        <li>Has plugins and themes built in + allows custom ones</li>
        <ul>
          <li>Code snippets</li>
          <li>Markdown</li>
          <li>Math</li>
        </ul>
        <li>No-code editor available online</li>
      </ul>
      <Note>So what are some of the features of reveal?</Note>
    </Slide> */}
  </Slide>
);

export default AboutRevealSlides;
